.wp-block-cover,
.wp-block-cover-image {
  min-height: unset;
}

.col__inner.has-border-radius {
  border-radius: var(--gs-col-radius, 10px);
}

.col__inner.has-drop-shadow {
  box-shadow: var(--gs-col-shadow, 3px 4px 20px rgba(0, 0, 0, 0.1));
